<template>
    <el-dialog class="add-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm" fullscreen :modal="false">
        <el-form ref="dialogForm" :model="dialogForm" :rules="teamRules" label-width="120px">
            <el-row :gutter="15">
              <el-col :span="8">
                  <el-form-item prop="houseId">
                    <el-popover placement="right" width="400" trigger="click" v-model="showPopover">
                        <techo-house ref="vCheckedHouse" :isShowHouse="true" :butName="'确定'" :disCodeOption="disCodeOption" :buildOptions="buildOptions" :houseOptions="houseOptions" 
                            @onRelaData="handleCheckedHouse" @onQueryBuild="handleQueryBuildList" @onQueryHouse="handleQueryHouseList"></techo-house>
                        <el-button class="check-house" slot="reference" type="danger" size="small">请选择房屋</el-button>
                    </el-popover>
                  </el-form-item>
              </el-col>
              <el-col :span="16">
                <el-form-item label="房屋地址">
                    <el-input v-model="dialogForm.address" disabled placeholder="请填写房屋地址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row :gutter="15">
              <el-col :span="8">
                <el-form-item label="出租用途" prop="leaseUse">
                    <el-select placeholder="请选择出租用途" clearable v-model="dialogForm.leaseUse">
                        <el-option v-for="(item, index) in dic['出租用途']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="隐患类型">
                    <el-select placeholder="请选择隐患类型" clearable v-model="dialogForm.dangerType">
                        <el-option v-for="(item, index) in dic['隐患类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="户号">
                    <el-input v-model="dialogForm.code" disabled placeholder="请填写户号"></el-input>
                </el-form-item>
                <el-form-item label="建筑面积" prop="buildingArea">
                    <el-input v-model="dialogForm.buildingArea" :maxlength="8" placeholder="请填写建筑面积"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="建筑用途">
                    <el-select placeholder="请选择建筑用途" clearable v-model="dialogForm.useful">
                        <el-option v-for="(item, index) in dic['建筑用途']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <div class="cut-apart-line"></div>
            <el-row :gutter="15">
              <el-col :span="8">
                <el-form-item label="房主姓名">
                    <el-input v-model="dialogForm.landlordName" disabled placeholder="请填写房主姓名"></el-input>
                </el-form-item>
                <el-form-item label="证件类型">
                    <el-select placeholder="请选择证件类型" clearable v-model="dialogForm.idcode">
                        <el-option v-for="(item, index) in dic['证件代码']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="房主电话" prop="landlordContact">
                    <el-input v-model="dialogForm.landlordContact" :maxlength="18" placeholder="请填写房主电话"></el-input>
                </el-form-item>
                <el-form-item label="证件号码">
                    <el-input v-model="dialogForm.cardnumber" :maxlength="18" placeholder="请填写证件号码"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="房主现住地">
                    <el-input v-model="dialogForm.landlordAddress" placeholder="请填写房主现住地"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <div class="cut-apart-line"></div>
            <el-row :gutter="15">
              <el-col :span="8">
                <el-form-item label="承租人姓名" prop="lesseeName">
                    <el-input v-model="dialogForm.lesseeName" :maxlength="18" placeholder="请填写承租人姓名"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="承租人电话" prop="lesseeContact">
                    <el-input v-model="dialogForm.lesseeContact" :maxlength="18" placeholder="请填写承租人电话"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="承租人身份证号" prop="lesseeCardnumber">
                    <el-input v-model="dialogForm.lesseeCardnumber" :maxlength="18" placeholder="请填写承租人身份证号"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/infact_people_service.js";
export default {
  props: {
    disCodeOption: {
      type: Array,
      default: function() {
        return [];
      }
    }
  },
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      buildOptions: [],
      houseOptions: [],
      showPopover: false,
      dic: {
        建筑用途: [],
        证件代码: [],
        出租用途: [],
        隐患类型: [],
        行政区划: []
      },
      dialogForm: {
        dangerType: "00", // 安全隐患类型
        houseId: "", // 户ID
        code: "", // 户号
        landlordName: "", // 房主姓名
        landlordContact: "", // 房主电话
        landlordAddress: "陕西省铜川市", // 房主现住地
        idcode: "111"
      },
      teamRules: {
        houseId: [{ required: true, message: "请选择房屋", trigger: "change" }],
        landlordContact: [
          { required: true, message: "请输入户主电话", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        leaseUse: [
          { required: true, message: "请选择出租用途", trigger: "change" }
        ],
        lesseeCardnumber: [
          { required: true, message: "请输入房主现住地", trigger: "blur" },
          util.validate.isIDCard()
        ],
        lesseeName: [
          { required: true, message: "请输入承租人姓名", trigger: "blur" },
          { min: 2, max: 12, message: "长度在 2 到 10 个字符", trigger: "blur" }
        ],
        lesseeContact: [
          { required: true, message: "请输入承租人电话", trigger: "blur" },
          util.validate.tellAndPhone()
        ],
        buildingArea: [util.validate.valiInteger()],
      }
    };
  },
  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },
  methods: {
    /** 选择现住地户 */
    handleCheckedHouse(value, buildInfo) {
      this.dialogForm.districtCode = buildInfo.districtCode;
      let houseInfo = _.find(this.houseOptions, item => {
        return item.id == value.houseId;
      });

      service.requestCodeByAddress(buildInfo.districtCode, data => {
        this.dialogForm.address =
          "陕西省" + data.belongname + data.name + houseInfo.name;
        this.showPopover = false;
      });
      this.dialogForm.houseId = houseInfo.id;
      this.dialogForm.code = houseInfo.houseNum; // 房号
      this.dialogForm.landlordName = houseInfo.householderName; // 房主姓名
      this.dialogForm.landlordContact = houseInfo.householderPhone; // 房主电话
      this.$refs["dialogForm"].clearValidate("houseId");
    },
    /** 查询楼栋列表 */
    handleQueryBuildList(code) {
      this.buildOptions = [];
      this.houseOptions = [];
      code != undefined && service.requestCodeByBuild(code, data => {
        this.buildOptions = data.list;
      });
    },
    /** 查询户列表 */
    handleQueryHouseList(param) {
      this.houseOptions = [];
      if (param.buildingId != "") {
        service.requestBuildByHouse(param, data => {
          this.houseOptions = data.list;
        });
      }
    },
    /** 保存 */
    submitForm() {
      this.$refs["dialogForm"].validate(valid => {
        if (valid) {
          let param = _.cloneDeep(this.dialogForm);
          param.dicMap && delete param.dicMap;
          if (this.isAdd == true) {
            // 添加
            service.requestSaveRentalHouse(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "出租房屋信息添加成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          } else if (this.isAdd == false) {
            // 修改
            service.requestUpdateRentalHouse(param, data => {
              this.$popup.showNotification({
                type: "success",
                message: "出租房屋信息修改成功！"
              });
              this.resetForm();
              this.$parent.handleQuery();
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$refs["vCheckedHouse"].resetForm();
      this.$data.dialogVisible = false;
      this.showPopover = false;
      this.isAdd = true;
      this.dialogForm = {
        dangerType: "00", // 安全隐患类型
        houseId: "", // 户ID
        code: "", // 户号
        landlordName: "", // 房主姓名
        landlordContact: "", // 房主电话
        landlordAddress: "陕西省铜川市", // 房主现住地
        idcode: "111"
      };
    }
  }
};
</script>
<style lang="scss">
.add-dialog {
  .el-date-editor.el-input {
    width: 100%;
  }
  .el-cascader {
    width: 100%;
  }
  .el-select {
    width: 100%;
  }
  .check-house {
    width: 100%;
  }
  .cut-apart-line {
    width: 100%;
    border-top: 1px solid #028aff;
    margin-bottom: 22px;
  }
}
</style>